import React, {useEffect, useState} from 'react';
import { Carousel, Image, Button } from 'antd';
import {Link} from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import BlogService from '../../BlogService';
import parse from 'html-react-parser';

import Config from '../../Config';
import pic1 from '../../Assets/pic1.jpg';
import pic2 from '../../Assets/pic2.jpg';
import pic3 from '../../Assets/pic3.jpg';
import pic5 from '../../Assets/pic5.jpg';
import pic6 from '../../Assets/pic6.jpg';
import pic10 from '../../Assets/pic10.jpg';
import stockpic1 from '../../Assets/stockpic1.jpg';
import honorat from '../../Assets/honorat.jpg';
import mapa from '../../Assets/mapa.jpg';
import './MainPage.css';


const MainPage = () => {

  const [blogPost, setBlogPost] = useState(null);

  useEffect(() => {
    getBlogPost()
    window.scrollTo(0, 0);
  }, []);

  const getBlogPost = async () => {
    const blogItem = await BlogService.getFlashNews();
    setBlogPost(blogItem);
  }

  const isMobile = useMediaQuery({ maxWidth: 767 });
  
  return(
    <div className='main-page sub-page'>
      <div className='section card title'>
        <h1>Parafia Rzymskokatolicka <br /> pw. b. Honorata Komiskiego <br /> w Bdzinie-Grodcu</h1>
      </div>
      {blogPost && blogPost.content !== "" && 
        <div className='section card special-info'>
          <div className='text'>
            {parse(blogPost.content)}
          </div>
        </div>
      }
      <div className='section most-important'>
        <div className='carousel'>
          {isMobile ?
            <Carousel autoplay={true} effect={'fade'}>
              <Image src={pic5} preview={false} alt='Pic1' className='carousel-image' />
              <Image src={pic6} preview={false} alt='Pic2' className='carousel-image' />
              <Image src={pic10} preview={false} alt='Pic3' className='carousel-image' />
            </Carousel>
            : 
            <Carousel autoplay={true} effect={'fade'}>
              <Image src={pic1} preview={false} alt='Pic1' className='carousel-image' />
              <Image src={pic2} preview={false} alt='Pic2' className='carousel-image' />
              <Image src={pic3} preview={false} alt='Pic3' className='carousel-image' />
            </Carousel>
          }

        </div>
      </div>
      <div className='section card newest'>
        <div className='special'>
          <Image src={stockpic1} preview={false} alt='pic' className='vertical-image' />
        </div>
        <div className='text'>
          <p>
            <h2>Msze wite w niedziel</h2>
            <ul>
              <li>8:00</li>
              <li>10:00</li>
              <li>11:30</li>
              <li>16:30</li>
            </ul>
            <h2>Msze wite w dni powszednie</h2>
            <ul>
              <li>17:30</li>
            </ul>
            <h2>Kancelaria parafialna czynna</h2>
            <ul>
              <li>W poniedziaek 18:10  19:00</li>
              <li>W czwartek 18:10  19:00</li>
            </ul>
          </p>
          <Link to={'/ogloszenia'}>
            <Button type="primary">Ogoszenia</Button>
          </Link>
        </div>
      </div>
      <div className='section card about reverted'>
        <div className='special'>
          <Image src={pic5} preview={false} alt='pic5' className='vertical-image' />
        </div>
        <div className='text'>
          <h2>O parafii</h2>
          <p>
            Parafia pw. bogosawionego Honorata Komiskiego zostaa powoana stosunkowo niedawno i jest najmodsz
            parafi w Bdzinie-Grodcu. Zaoono j w celu posugi duszpasterskiej dla mieszkacw kopalnianego osiedla
            przy ulicy Konopnickiej. 
          </p>
          <p>
            Cho ustanowienia parafii dokona biskup czstochowski Stanisaw Nowak, obecnie naley ona do Diecezji Sosnowieckiej.
            Biskup Adam migielski dokona jej uroczystej konsekracji 15 padziernika 2000 r.
          </p>
          <p>
            W parafii dziaa Koo ywego Raca. W kad rod zmawiana jest Nowenna do Matki Boej Nieustajcej pomocy, a w pitki
            Koronka do Boego Miosierdzia.
          </p>
          <Link to={'/o-parafii'}>
            <Button type="primary">Wicej o parafii</Button>
          </Link>
        </div>
      </div>
      <div className='section card patron'>
        <div className='special'>
          <Image src={honorat} preview={false} alt='Pic7' className='vertical-image' />
        </div>
        <div className='text'>
          <h2>Bogosawiony Honorat Komiski</h2>
          <p>
            ycie bogosawionego Honorata pokazuje, e Pan Bg ma dla kadego z nas plan, oraz e drog do witoci poprzedza
            czasem zwtpienie i kryzys wiary. Buntownik z pobonej inteligenckiej rodziny sta si jedn z najwaniejszych postaci
            ycia religijnego polakw pod zaborami.
          </p>
          <p>
            W modoci, idc za przykadem ojca, uczy si na architekta. Wstpi do pockiego gimnazjum. Zostaje dowiadczony
            na wiele sposobw, modzieczym niedowiarstwem, przedwczesn mierc ojca, a wreszcie wtrceniem do carskiego wizienia.
            Z warszawskiej Cytadeli wyszed odmieniony. Po przeytym nawrceniu sta si spokojny i pogodzony z Bogiem. Niedugo pniej
            rozpocz swoje ycie zakonne jako Kapucyn.
          </p>
          <p>
            W trudnych warunkach zaborw, nieustannie podejrzewany przez wadz rosyjskie o dziaalno wywrotow i konspiracj, zaoy
            23 zgromadzenia zakonne i 26 stowarzysze tercjarskich. Jednoczenie te promowa ukryte zgromadzenia zakonne, jako form
            oporu wobec rosyjskiej opresji. By uznanym spowiednikiem, tytanem pracy i niestrudzonym organizatorem. Zmar w 1916 r. w 
            opinii witoci.
          </p>
        </div>
      </div>
      <div className='section card map reverted'>
        <div className='special'>
          <Image src={mapa} preview={false} alt='mapa' className='vertical-image' />
        </div>
        <div className='text'>
          <h3>Marii Konopnickiej 11A</h3>
          <h3>42-506 Bdzin</h3>
          <h3>Email: parafiahonorat@gmail.com</h3>
          <h3>Tel: 690-649-750</h3>
          <a href={Config.googleMapLink} target="_blank" rel="noreferrer">
            <Button type="primary">Zobacz na mapie</Button>
          </a>
          <h3>Numer konta:</h3>
          <h3>77 8591 0007 0410 0690 4678 0002</h3>
          <h3>Przelewy prosimy skada z dopiskiem "Darowizna".</h3>
          <h3>Za wszelkie datki serdeczne Bg zapa!</h3>
        </div>
      </div>
    </div>
  )
}

export default MainPage;