import React, {useEffect} from 'react';
import './About.css';
import {Image} from 'antd';
import pic21 from '../../Assets/pic21.jpg';

const About = () => {

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return(
    <div className='about-page sub-page'>
        <h1>O parafii</h1>
        <div className='section card history reverted'>
          <div className='special'>
            <Image src={pic21} preview={false} alt='pic' className='vertical-image' />
          </div>
          <div className='text'>
            <h1>Krtka historia parafii</h1>
            <p>
              Historia parafii siga 1989 roku. Z powodu rozlegoci i iloci wiernych parafii pw. w. Katarzyny,
              biskup ordynariusz Stanisaw Nowak zleci stworzenie wikariatu terenowego. Pierwszy jego duszpasterz 
              ksidz Leszek Koczyk, pozyska od dyrekcji kopalni "Grodziec" lokal dla tworzcej si parafii.
            </p>
            <p>
              Pierwsza kaplica zaoona zostaa w baraku otrzymanym z parafii w. Krzysztofa w Tychach. Ju w 1990
              odprawiono tam pierwsz msz i wybrano bogosawionego Honorata Komiskiego na patrona nowo powstajcej
              parafii.
            </p>
            <p>
              Rwno w nowej parafii zaczto budow nowego kocioa. Delegacja parafian spotkaa si z Ojcem witym
              Janem Pawem II, by ten pobogosawi kamie wgielny. Uroczystego wmurowania kamienia dokona biskup
              nowo utworzonej diecezji sosnowieckiej Adam migielski.
            </p>
            <p>
              Po prawie dzisiciu latach udao si zakoczy budow. Koci ma plan szeciokta foremnego i moe pomieci
              okoo 500 osb. Szybka i sprawna budowa udaa si dziki wspaniaej ofiarnoci parafian. W 2000 roku nastpia
              uroczysta konsekracja witynii.
            </p>
          </div>
        </div>
    </div>
  )
}

export default About;